html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter";
  overflow: auto;
  overscroll-behavior: none;
  background-color: white;
  height: 100%;
  width: 100%;
}
#root {
  height: 100%;
  width: 100%;
  margin: 0px;
  padding: 0px;
}

.dark-mode-button {
  float: right;
  margin: 40px;
  margin-right: 60px;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color: black;
  color: white;
}

h1 {
  font-weight: 400;
  margin-bottom: none;
  margin-left: 40px;
}
.workspaces-container {
  height: 100%;
  min-height: 100%;
  min-width: 300px;
  display: inline-flex;
  flex-direction: column;
  max-width: 20%;
}

.all-workspace,
.rest-workspaces {
  cursor: pointer;
  padding: 30px;
}
.all-workspace:hover,
.rest-workspaces:hover {
  transform: scale(1.1);
  background-color: rgb(220, 220, 220);
  color: rgb(47, 47, 47);
  transition: all 0.1s ease-in-out;
}

.selected-workspaces {
  background-color: rgb(47, 47, 47) !important;
  color: white !important;
}

.overlay {
  pointer-events: none;
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(153, 125, 125, 0);
  mix-blend-mode: difference;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.expand {
  width: 100%;
  height: 100%;
  background-color: rgba(230, 230, 230, 1);
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.footer {
  position: fixed;
  bottom: 15px;
  left: 4rem;
  font-size: 0.9rem;
  transition: 0.3s color ease-in-out;
}
.absolute-text-dark {
  color: rgba(220, 220, 220, 1);
  transition: 0.3s color ease-in-out;
}
