.item-input {
  border: none;
  border-bottom: 2.5px black solid;
  outline: none;
  background-color: transparent;
  font-size: large;
  padding-bottom: 5px;
  width: 80%;
}

.item-input:hover {
  border-bottom: 3px solid lawngreen;
}
.item-content {
  display: inline;
}
.item-remove {
  font-size: 20pt;
  float: right;
  height: 20px;
  width: 20px;
  border: none;
  background-color: transparent;
}

.item-remove:hover {
  color: red;
}

.item-content-completed {
  text-decoration: line-through 5px solid;
  color: rgba(47, 47, 47, 0.8);
}
.item-completer {
  font-size: 20pt;
  float: right;
  height: 20px;
  width: 20px;
  border: none;
  background-color: transparent;
  margin-right: 20px;
}
.item-completer:hover {
  color: green;
}
